.color-switcher {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.color-switcher__btn {
  margin-bottom: 20px;
  margin-right: 10px;
  padding: 5px 10px;
}

#change-bg-spead-control {
  margin: 0, auto;
  width: 100%;
  margin-bottom: 10px;
  display: block;
}

.color-switcher_label {
  background-color: #fff;
  border-radius: 10px;
  padding: 5px;
  font-weight: bold;
}

